<template>
    <div style="height: 100%">
        <el-form label-position="left" label-width="140px">
                <el-form-item label="数据源" >
                    <el-select v-model="dataSourceInfoTemp" value-key="dataSourceId" placeholder="请选择数据源">
                        <el-option
                                v-for="option in dataSourceInfoList"
                                :key="option.dataSourceId"
                                :label="option.dataSourceName+' ' +option.dataSourceIP +':'+option.dataSourcePort"
                                :value="option"
                        ></el-option>
                    </el-select>
                    <el-button type="primary" style="margin-left: 30px" @click="onShowTable">展示</el-button>
                </el-form-item>
        </el-form>
        <el-table
                :data="tableData"
                style="width: 100%"
                height="90%">
            <el-table-column
                    fixed
                    :key="col"
                    :prop="col"
                    :label="col"
                    width="150"
                    v-for="col in tableCols">
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import axios from "axios";
    export default {
        name: "ShowData",
        data () {
            return {
                //下拉框对应的数据库信息对象
                dataSourceInfoTemp:{
                },
                //保存数据库信息
                dataSourceInfoList: [],
                //保存对应表信息
                tableData:[],
                //保存表头
                tableCols:[]
            }
        },
        mounted(){
            //获取数据源列表
            this.getDataSourceInfo();
        },
        methods:{
            //获取所有数据源信息
            getDataSourceInfo() {
                axios.post('http://localhost:8888/getTableDataSourceInfo')
                    .then((response) =>{
                            console.log(response)
                            this.dataSourceInfoList=response.data
                            console.log(this.dataSourceInfoList)
                        },(error) => {
                            this.$message.error('查询失败'+error.response.data.msg)
                        }
                    )
            },
            //获取数据源对应的表信息
            onShowTable(){
                console.log("onShowTable")
                axios.post('http://localhost:8888/getTableByDataSource',this.dataSourceInfoTemp)
                    .then((response) => {
                            console.log(response)
                            this.tableCols = response.data.colNames
                            console.log(this.cols)

                            this.tableData = response.data.dataList.map(x=>{
                                let obj={}
                                for(let i=0;i<this.tableCols.length;i++){
                                    obj[this.tableCols[i]]=x[i];
                                }
                                return obj
                            })
                        }, (error) => {
                            this.$message.error('查询失败' + error.response.data.msg)
                        }
                    )

            }
        }
    }
</script>

<style scoped>

</style>
