<template>
    <div style="height: 100%">
        <el-form label-position="left" label-width="140px">
            <el-form-item label="数据源" >
                <el-select v-model="dataSourceInfoTemp1" value-key="dataSourceId" placeholder="请选择数据源1">
                    <el-option
                            v-for="option in dataSourceInfoList"
                            :key="option.dataSourceId"
                            :label="option.dataSourceName+' ' +option.dataSourceIP +':'+option.dataSourcePort"
                            :value="option"
                    ></el-option>
                </el-select>
                <el-select v-model="dataSourceInfoTemp2" value-key="dataSourceId" placeholder="请选择数据源2" style="margin-left: 30px">
                    <el-option
                            v-for="option in dataSourceInfoList"
                            :key="option.dataSourceId"
                            :label="option.dataSourceName+' ' +option.dataSourceIP +':'+option.dataSourcePort"
                            :value="option"
                    ></el-option>
                </el-select>
                <el-button type="primary" style="margin-left: 30px" @click="onShowTable">合并</el-button>
            </el-form-item>
        </el-form>
        <el-table
                :data="tableData"
                style="width: 100%"
                height="90%">
            <el-table-column
                    fixed
                    :key="col"
                    :prop="col"
                    :label="col"
                    width="150"
                    v-for="col in tableCols">
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "MergeData",
        data() {
            return {
                //保存查询后返回的表列名和值
                tableData: [],
                tableCols: [],
                //两个Select对应的表对象
                dataSourceInfoTwo:[{},{}],
                dataSourceInfoTemp1: {},
                dataSourceInfoTemp2: {},
                //从元数据库中得到的所有数据源信息列表
                dataSourceInfoList: []
            }
        },
        mounted(){
            //获取数据源列表
            this.getDataSourceInfo();
        },
        methods:{
            //获取所有数据源信息
            getDataSourceInfo() {
                axios.post('http://localhost:8888/getTableDataSourceInfo')
                    .then((response) =>{
                            console.log(response)
                            this.dataSourceInfoList=response.data
                            console.log(this.dataSourceInfoList)
                        },(error) => {
                            this.$message.error('查询失败'+error.response.data.msg)
                        }
                    )
            },
            onShowTable(){
                console.log(this.dataSourceInfoTemp1)
                console.log(this.dataSourceInfoTemp2)
                if(this.dataSourceInfoTemp1===this.dataSourceInfoTemp2)
                {
                    alert("warn：两次选择的数据源一致！！！")
                    return
                }
                if(JSON.stringify(this.dataSourceInfoTemp1)==='{}'||JSON.stringify(this.dataSourceInfoTemp1)==='{}')
                {
                    alert("warn：未选择数据源！！！")
                    return
                }
                this.dataSourceInfoTwo[0]=this.dataSourceInfoTemp1;
                this.dataSourceInfoTwo[1]=this.dataSourceInfoTemp2;
                axios.post('http://localhost:8888/mergeTableByDataSource',
                    this.dataSourceInfoTwo
                )
                    .then((response) => {
                            console.log(response)
                            this.tableCols = response.data.colNames
                            console.log(this.cols)

                            this.tableData = response.data.dataList.map(x=>{
                                let obj={}
                                for(let i=0;i<this.tableCols.length;i++){
                                    obj[this.tableCols[i]]=x[i];
                                }
                                return obj
                            })
                        }, (error) => {
                            this.$message.error('查询失败' + error.response.data.msg)
                        }
                    )

            }
        }

    }
</script>

