<template>
    <el-table
            :data="tableDataSourceInfo"
            style="width: 100%"
            height="90%">
        <el-table-column
                fixed
                :key="col"
                :prop="col"
                :label="col"
                width="150"
                v-for="col in cols">
        </el-table-column>
        <el-table-column
                fixed="right"
                label="操作"
                width="100">
            <template slot-scope="scope">
                <el-button @click="delDataSourceInfoClick(scope.row)" type="text" size="small">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
</template>

<script>
import axios from 'axios'
import qs from 'qs'

export default {
    name: "ShowDataSource",
    data() {
        return {
            tableDataSourceInfo: [],
            cols: []
        }
    },
    mounted() {
        console.log("mounted")
        this.getTableDataSourceInfo()
    },
    methods: {
        //从后台获取数据源信息
        getTableDataSourceInfo() {
            axios.post('http://localhost:8888/getTableDataSourceInfo')
            .then((response) =>{
                console.log(response)
                this.cols = Object.keys(response.data[0])
                console.log(this.cols)
                this.tableDataSourceInfo=response.data
            },(error) => {
                this.$message.error('查询失败'+error.response.data.msg)
                }
            )
        },
        //删除数据源信息
        delDataSourceInfoClick(row) {
            console.log(row.dataSourceId);
            axios.post('http://localhost:8888/delByDataSourceId',qs.stringify(
                {
                    dataSourceId: row.dataSourceId
                    }))
                .then((response)=>{
                    console.log(response)
                    this.getTableDataSourceInfo()
                }
            )
        }
    }
}
</script>

